img.item {
  width: 92%;
  height: 55vh;
  object-fit: cover;
  margin-bottom: 2.5rem;
  z-index: 1;
  overflow: hidden;
  position: relative;
  border: 0.25rem double #b8af9e;
  cursor: pointer;
  animation: fadeInImg ease-in 1;
  animation-duration: 0.5s;
}

.product-name-container {
  flex-basis: 10%;
}

.loading {
  min-width: 95%;
  height: 55vh;
  margin-bottom: 2.5rem;
  z-index: 1;
  overflow: hidden;
  position: relative;
  border: 0.25rem double #b8af9e;
  filter: blur(1.2rem);
}

@keyframes fadeInImg {
  0% {
    opacity: 0.5;
    filter: blur(0.6rem);
  }
  50% {
    opacity: 0.7;
    filter: blur(0.3rem);
  }
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
/*  */

#bootstrap-overrides h2.collection-name {
  padding-left: 1.1rem;
  padding-right: 1.1rem;
  line-height: 1.75rem;
  border-bottom: 0.02px solid black;
  text-shadow: 0.1rem 0.1rem 0.6rem rgb(0, 0, 0);
}

#bootstrap-overrides .jumbotron.beige {
  background: linear-gradient(to top, #efeee9, #efeee9);
  border-radius: 0;
  padding-top: 3.7rem;
  position: relative;
}

#bootstrap-overrides .jumbotron.beige::after {
  content: "";
  width: 100%;
  background: linear-gradient(to top, #7a5309, #efeee9);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.3;
}

.border-name-left {
  border-left: 0.2rem solid rgb(102, 102, 102);
  line-height: 1rem;
  font-size: 0.8rem;
}

.vertical {
  font-family: "Yesteryear", Helvetica, cursive;
  position: absolute;
  color: #fcf9f9ef;
  writing-mode: vertical-lr;
  transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  z-index: 2;
  bottom: 3.5%;
  right: 3.3%;
  font-size: 1.9rem;
  cursor: default;
}

.vertical span.dirty {
  font-family: "Rock Salt", Geneva, sans-serif;
  font-size: 1.2rem;
}

/* Media Queries */
@media (max-width: 768px) {
  .vertical {
    font-size: 1.2rem;
    bottom: 8%;
    right: 6%;
  }
  .vertical span.dirty {
    font-size: 0.8rem;
  }
  .loading {
    height: 40vh;
    width: 96%;
  }

  .border-name-left {
    border-left: 0.15rem solid rgb(102, 102, 102);
    line-height: 0.7rem;
    font-size: 0.7rem;
  }
  img.item {
    height: 40vh;
    width: 96%;
  }

  #bootstrap-overrides .small-screen-fix {
    padding-left: 9px;
    padding-right: 9px;
  }
}
